import { types } from "../types/types"
const initialState={
  alumnos: [],
  active: null
}
export const alumnosReducer = (state= initialState, action) => {
  switch (action.type) {
    case types.alumnosActive:
      return{
        ...state,//retorna las notas yla nota activa que esta en el payload
        active: {
          ...action.payload
        }
      }
    case types.alumnosAddNew:
      return {
          ...state,
          alumnos: [action.payload, ...state.alumnos]
      }
    case types.alumnosLoad:
      return {
        ...state,
        alumnos: [...action.payload]
      }
    case types.alumnosUpdated:
      return {
        ...state,
        alumnos: state.alumnos.map(alumno=>alumno.id===action.payload.id?action.payload.alumno:alumno)
      }
    
    case types.alumnosDelete:
      return {
        ...state, 
        active: null,
        alumnos: state.alumnos.filter(alumno=>alumno.id!==action.payload)
      }
    case types.alumnosLogoutCleaning:
      return {
        ...state, 
        active: null,
        alumnos: []
      }
    default:
      return state;
  }
}
