import { types } from "../types/types"
const initialState={
  cursos: [],
  active: null
}
export const cursoReducer = (state= initialState, action) => {
  switch (action.type) {
    case types.cursosActive:
      return{
        ...state,//retorna las notas yla nota activa que esta en el payload
        active: {
          ...action.payload
        }
      }
    case types.cursosAddNew:
      return {
          ...state,
          cursos: [action.payload, ...state.cursos]
      }
    case types.cursosLoad:
      return {
        ...state,
        cursos: [...action.payload]
      }
    case types.cursosUpdated:
      return {
        ...state,
        cursos: state.cursos.map(curso=>curso.id===action.payload.id?action.payload.curso:curso)
      }
    
    case types.cursosDelete:
      return {
        ...state, 
        active: null,
        cursos: state.cursos.filter(curso=>curso.id!==action.payload)
      }
    case types.cursosLogoutCleaning:
      return {
        ...state, 
        active: null,
        cursos: []
      }
    default:
      return state;
  }
}