import { loadCursos, loadCursoCuenta } from "../helpers/loadCursos"
import { types } from "../types/types"


export const startLoadingCursos=()=>{
  return async(dispatch)=>{
    const cursos= await loadCursos();
    dispatch(setCursos(cursos));
  }
}

export const setCursos=(cursos)=>({
  type: types.cursosLoad,
  payload: cursos
})

export const activeCurso=( id, cursos)=>{
  return async(dispatch)=>{
    let cursoTransacciones= await loadCursoCuenta(id);
    let curso = cursos.filter(e=>e.id===id)[0]
    curso = {...curso, transacciones: cursoTransacciones}
    dispatch(setActivo(id, curso))
  }
};

export const setActivo=( id, curso )=>({
  type: types.cursosActive,// genera accion tipo nota activa
  payload: {// retorna un objeto con id del ususario y la nota
    id, 
    ...curso
  }
});