import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { startLogOut } from '../../actions/auth'
import { activeCurso } from '../../actions/cursos'
import { HomeListCursos } from './HomeListCursos'


export const HomeSidebar = () => {

  const dispatch = useDispatch()
  const {name}= useSelector(state=>state.auth)// para recueprar el nombre del state
  const {cursos}= useSelector(state => state.cursos)
  // const {alumnos}= useSelector(state => state.alumnos)
  const hanleLogout=()=>{
    dispatch(startLogOut())
  }
  const handleSelect=(e)=>{
    dispatch(activeCurso(e.target.value, cursos))
  }
  return (
    <aside className='home__sidebar primary'>
      <div className='home__sidebar-navbar'>
        <h3 className='mt-5'>
          <i className='far fa-moon'></i>
          <span>{name}</span>
        </h3>
        <select name="cursos" id="cursos" defaultValue="" onChange={handleSelect}>
          <option value="" disabled hidden>Elija Curso</option>
          {
            cursos.map(curso => (
              <HomeListCursos key={curso.id} {...curso}/>
            ))
          }
        </select>
        <button 
          className='btn'
          onClick={hanleLogout}
        >
          Logout
        </button>
      </div>
    </aside>
  )
}
