import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { activeAlumno } from '../../../actions/alumnos'
import { completarArray } from '../../../helpers/miniHelps'
import { HomeListCursos } from '../HomeListCursos'
// import { HomeListCursos } from './HomeListCursos'

export const HomeHallCurso = () => {
  const dispatch = useDispatch()
  const  {active}= useSelector(state => state.cursos)
  const  alumnosDatos= useSelector(state => state.alumnos.alumnos)
  const {lAlumnos}=active
  const dCAlumnosCurso = completarArray(lAlumnos, alumnosDatos)
  const handleSelect=(e)=>{
    dispatch(activeAlumno(e.target.value, active, alumnosDatos))
  }
  return (
    <>
      <select name="alumnos" id="alumnos" defaultValue="" onChange={handleSelect}>
          <option value="" disabled hidden>Elija Alumno</option>
          <option value={active.id}>{active.nCurso}</option>
          {
            (dCAlumnosCurso[0])&&dCAlumnosCurso.map(al=>(<HomeListCursos key={al.id} {...al}/>))
          }
        </select>
    </>
  )
}
