import { loadAlumnos } from "../helpers/loadCursos"
import { types } from "../types/types"

export const startLoadingAlumnos=()=>{
  return async(dispatch)=>{
    const alumnos= await loadAlumnos();
    dispatch(setAlumnos(alumnos));
  }
}

export const setAlumnos=(alumnos)=>({
  type: types.alumnosLoad,
  payload: alumnos
})

export const activeAlumno=(id, cursoActivo, alumnos)=>{
  return (dispatch)=>{
    let alumno = alumnos.find(e=>e.id===id)
    alumno= {...alumno, transacciones: cursoActivo.transacciones.filter(transacion=>transacion.asignacion.find(e=>e===id))}
    dispatch(setActivoAlumno(id, alumno))
  }
}

export const setActivoAlumno=( id, alumno )=>({
  type: types.alumnosActive,// genera accion tipo nota activa
  payload: {// retorna un objeto con id del ususario y la nota
    id, 
    ...alumno
  }
});
