import { db } from "../firebase/firebaseConfig"

export const loadCursos = async () => {
  const cursosSnap= await db.collection(`colegio/fLxRGsMYLcRqRcqH2I9Y/cursos`).get();
  const cursos=[]
  cursosSnap.forEach(snapHijo=>{
    cursos.push({
      id: snapHijo.id,
      ...snapHijo.data()
    })
  })
  return cursos;
}

export const loadCursoCuenta= async(id)=>{
  const cuentasSnap= await db.collection(`colegio/fLxRGsMYLcRqRcqH2I9Y/cursos/${id}/operaciones`).get();
  const cuentas=[]
  cuentasSnap.forEach(snapHijo=>{
    cuentas.push({
      id: snapHijo.id,
      ...snapHijo.data()
    })
  })
  return cuentas;
}

